import { Component, Input } from '@angular/core';

@Component({
  selector: 'm-mdl-icon',
  template: `
    <ng-container [ngSwitch]="icon">
      <ng-container *ngSwitchCase="'bank'">
        <svg [attr.fill]="color" [ngClass]="['m-mdl-icon-' + icon]" viewBox="0 0 24 24" width="1em" height="1em" xmlns="http://www.w3.org/2000/svg">
          <path d="M0 0h24v24H0z" fill="none"/>
          <path d="M4 10v7h3v-7H4zm6 0v7h3v-7h-3zM2 22h19v-3H2v3zm14-12v7h3v-7h-3zm-4.5-9L2 6v2h19V6l-9.5-5z"/>
        </svg>
      </ng-container>
      <ng-container *ngSwitchCase="'explicit'">
        <svg [attr.fill]="color" [ngClass]="['m-mdl-icon-' + icon]" viewBox="0 0 24 24" width="2em" height="2em" xmlns="http://www.w3.org/2000/svg">
          <path d="M0 0h24v24H0z" fill="none"/>
          <path d="M19 3H5c-1.1 0-2 .9-2 2v14c0 1.1.9 2 2 2h14c1.1 0 2-.9 2-2V5c0-1.1-.9-2-2-2zm-4 6h-4v2h4v2h-4v2h4v2H9V7h6v2z"/>
        </svg>
      </ng-container>
    </ng-container>
  `
})
export class MDLIcon {
  @Input() icon: string;
  @Input() color: string = '#ffffff';
}



